@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;400;500;700;800&display=swap);
.RPCard {
  width: 80%;

  color: var(--Black10);
  background-color: var(--Black90);
  border-radius: var(--CornerRadius);
  padding: 2em;
}

.cardHeight {
  height: var(--CardHeightNarrow);
}

@media screen and (min-width: 750px) {
  .cardHeight {
    height: var(--CardHeight);
  }
}

.titleFontSize {
  font-size: 2rem;
}

@media screen and (min-width: 550px) {
  .titleFontSize {
    font-size: 3rem;
  }
}

.smolSpace {
  margin-right: 0.1em;
}

.title {
  font-weight: 300;
  font-family: "JetBrains Mono", monospace;
  color: white;
}
.header {
  margin-bottom: 0px;
}

.subHeader {
  font-size: 1.5rem;
  font-weight: 300;
  font-family: "JetBrains Mono", monospace;
  color: var(--Black50);
  margin-top: 0px;
}

.RPButton {
  color: var(--Black10);
  background-color: var(--Black90);
  border-radius: var(--CornerRadius);
  padding: 1em;
  border-color: var(--Black10);
  border-style: none;
  transition: var(--TransitionTime);
}

.RPButton:hover {
  background-color: var(--Black80);
  transition: var(--TransitionTime);
  cursor: pointer;
}

.about {
  display: table-cell;
  vertical-align: middle;
  height: 100vh;
}

.flexDirection {
  flex-flow: column;
}

.centerText {
  text-align: left;
}

.imgRightGap {
  margin-right: 2em;
}

@media screen and (max-width: 750px) {
  .flexDirection {
    flex-flow: row;
  }

  .imgRightGap {
    margin-right: 0em;
  }

  .centerText {
    text-align: center;
  }
}

.aboutContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.imgContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.selfie {
  --imgDimension: 12em;
  height: var(--imgDimension);
  width: var(--imgDimension);
  padding: 2em;
  border-radius: var(--CornerRadius);

  --outline-size: 2px;
  outline: var(--outline-size) solid white;
  outline-offset: calc(var(--outline-size) * -1);

  aspect-ratio: 4000/4000;
  background-size: cover;
  background-image: url(/static/media/goodPhoto.2677ec65.jpg);
}

.buttonGap {
  margin-left: 2em;
}

.appGridContainer {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 10px;
}

.downArrowContainer {
  cursor: pointer;
}

.fadeInArrow {
  opacity: 0;
  -webkit-animation: fadein ease-in var(--FadeTime) forwards;
          animation: fadein ease-in var(--FadeTime) forwards;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  position: fixed;
  top: 95%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.fadeOutArrow {
  opacity: 1;
  -webkit-animation: fadeout ease-in var(--FadeTime) forwards;
          animation: fadeout ease-in var(--FadeTime) forwards;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  position: fixed;
  top: 95%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.downArrow {
  color: white;
  font-size: 5rem;
  cursor: pointer;
}

.headerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.header {
  max-width: 1000px;
  width: 100%;

  opacity: 0;
  -webkit-animation: fadein ease-in var(--FadeTime) forwards;
          animation: fadein ease-in var(--FadeTime) forwards;
  -webkit-animation-delay: var(--TransitionTime);
          animation-delay: var(--TransitionTime);
}

.view1 {
  grid-column-start: 2;
  grid-column-end: 12;
  display: table;
  height: 100vh;
}

.view2 {
  /* background-color: aliceblue; */
  grid-column-start: 2;
  grid-column-end: 12;
  padding: 32px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.view3 {
  /* background-color: aliceblue; */
  grid-column-start: 4;
  grid-column-end: 10;
  height: 100vh;
}

.cardGap {
  margin-top: 32px;
}

.cardsContainer {
  display: flex;
  justify-content: center;
}

.flexDirection {
  flex-flow: row;
}

.cardImageSizeHorizontal {
  height: 100%;
  width: -webkit-max-content;
  width: max-content;
}

.cardImageSizeVertical {
  height: 100%;
  width: -webkit-max-content;
  width: max-content;
}

.cardImage {
  margin-right: 2em;
  box-shadow: 1px 2px 8px var(--Black);
}

@media screen and (max-width: 750px) {
  .flexDirection {
    flex-flow: column;
  }

  .cardImage {
    margin-right: 0em;
    box-shadow: 1px 2px 8px var(--Black);
  }

  .cardImageSizeHorizontal {
    height: -webkit-max-content;
    height: max-content;
    width: 100%;
  }

  .cardImageSizeVertical {
    height: -webkit-max-content;
    height: max-content;
    width: 100%;
  }

  .view2Columns {
    grid-column-start: 1;
    grid-column-end: 13;
  }

  .view3 {
    /* background-color: aliceblue; */
    grid-column-start: 1;
    grid-column-end: 13;
    height: 100vh;
  }

  .cardContainerVertical {
    justify-content: center;
    align-items: center;
  }

  .cardContainerHorizontal {
    justify-content: center;
  }
}

@media screen and (max-width: 400px) {
  .cardImageSizeVertical {
    height: -webkit-max-content;
    height: max-content;
    width: 100%;
  }
}

.cardContainerVertical {
  height: 100%;
  width: 100%;
  display: flex;
}

.cardContainerHorizontal {
  height: 100%;
  width: 100%;
  display: flex;
}

.oglp {
  background-color: #62626299;
  transition: var(--TransitionTime);
}

.oglp:hover {
  background-color: #62626278;
  transition: var(--TransitionTime);
  cursor: pointer;
}

.loap {
  background-color: #971fff78;
  transition: var(--TransitionTime);
}

.loap:hover {
  background-color: #971fff69;
  transition: var(--TransitionTime);
  cursor: pointer;
}

.rayTracer {
  background-color: #c500488d;
  transition: var(--TransitionTime);
}

.rayTracer:hover {
  background-color: #c500486a;
  transition: var(--TransitionTime);
  cursor: pointer;
}

.vpeq {
  background-color: #bbbbbb78;
  transition: var(--TransitionTime);
}

.vpeq:hover {
  background-color: #a7a7a769;
  transition: var(--TransitionTime);
  cursor: pointer;
}

.wave {
  aspect-ratio: 960/1620;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url(/static/media/waves.abeb35d7.svg);
}

.projects {
  width: 80%;
}

.projectsContainer {
  /* width: 90%; */
  /* margin-left: 10px; */
  display: flex;
  justify-content: center;
}

/* --- Globals --- */

:root {
  --Primary: #a11d6a;
  --Secondary: #951fff;
  --Linear: linear-gradient(to right, var(--Primary), var(var(--Secondary)));
  --Error: #e00248;

  --Black: #262626;
  --Black90: #333333;
  --Black80: #444444;
  --Black75: #666666;
  --Black50: #a6a6a6;
  --Black25: #e6e6e6;
  --Black10: #f2f2f2;

  --CornerRadius: 0.5em;

  --CardHeight: 128px;
  --CardHeightNarrow: 512px;
  --CardWidth: min(1280px, 100%);

  --TransitionTime: 0.2s;
  --FadeTime: 0.5s;
}

.whiteOutline {
  --outline-size: 2px;
  outline: var(--outline-size) solid white;
  outline-offset: calc(var(--outline-size) * -1);
}

.greyOutline {
  --outline-size: 2px;
  outline: var(--outline-size) solid #666666;
  outline: var(--outline-size) solid var(--Black75);
  outline-offset: calc(var(--outline-size) * -1);
}

.marginTop2em {
  margin-top: 2em;
}

.Monospace {
  font-size: 0.875rem;
  font-weight: 300;
  font-family: "JetBrains Mono", monospace;
  color: white;
}

.Paragraph,
p {
  font-size: 0.9rem;
  font-weight: 100;
  font-family: "JetBrains Mono", monospace;
  color: white;
}

.h1 {
  font-size: 7.063rem;
  font-weight: 600;
  font-family: "JetBrains Mono", monospace;
  color: white;
}

.h2 {
  font-size: 5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

.h3 {
  font-size: 3rem;
  font-weight: 1000;
  font-family: "Poppins", sans-serif;
  color: white;
}

.h4 {
  font-size: 2.5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

.h5 {
  font-size: 1.75rem;
  font-weight: 200;
  font-family: "Poppins", sans-serif;
  color: #a6a6a6;
  color: var(--Black50);
}

.h6 {
  font-size: 1rem;
  font-weight: 100;
  font-family: "Poppins", sans-serif;
  color: #666666;
  color: var(--Black75);
}

html,
body {
  background: #262626;
  scrollbar-width: none;
  scroll-behavior: smooth;
  /* height: calc(100vh - 40px); */
  margin: 0px;
}

* {
  -ms-overflow-style: none;
}
::-webkit-scrollbar {
  display: none;
}

